import { getPath } from '@/utils/tools';
import React, { useState, useEffect, useRef } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';

export function Header(props: any) {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
    const mobileMenuRef = useRef<HTMLDivElement>(null);
    const menuButtonRef = useRef<HTMLButtonElement>(null);
    
    // Gerencia foco quando menu mobile abre/fecha
    useEffect(() => {
        if (isMobileMenuOpen && mobileMenuRef.current) {
            // Define primeiro item do menu como focvel quando menu abre
            const firstMenuItem = mobileMenuRef.current.querySelector('a');
            if (firstMenuItem) {
                (firstMenuItem as HTMLElement).focus();
            }
        } else if (!isMobileMenuOpen && menuButtonRef.current) {
            // Retorna o foco ao boto quando o menu fecha
            menuButtonRef.current.focus();
        }
    }, [isMobileMenuOpen]);

    // Fecha menu ao pressionar ESC
    useEffect(() => {
        const handleKeyDown = (e: KeyboardEvent) => {
            if (e.key === 'Escape' && isMobileMenuOpen) {
                setIsMobileMenuOpen(false);
            }
        };
        
        window.addEventListener('keydown', handleKeyDown);
        return () => window.removeEventListener('keydown', handleKeyDown);
    }, [isMobileMenuOpen]);

    const toggleMenu = () => {
        setIsMobileMenuOpen(!isMobileMenuOpen);
        // Anuncia mudana de estado para leitores de tela
        const message = !isMobileMenuOpen ? "Menu de navegao aberto" : "Menu de navegao fechado";
        const announcement = document.createElement('div');
        announcement.setAttribute('role', 'status');
        announcement.setAttribute('aria-live', 'polite');
        announcement.className = 'sr-only';
        announcement.textContent = message;
        document.body.appendChild(announcement);
        setTimeout(() => document.body.removeChild(announcement), 1000);
    };

    return (
        <div className="bg-gray-50 text-gray-800 font-sans">
            <header className="bg-white shadow-md" role="banner">
                <div className="container mx-auto px-4">
                    <div className="flex justify-between items-center py-4">
                        <div className="flex items-center">
                            <img 
                                src="https://upload.wikimedia.org/wikipedia/commons/7/77/Marca-da-UEPB-Aplica%C3%A7%C3%A3o-Colorida-em-PNG-1.png" 
                                alt="Logo da Universidade UEPB - Departamento de Cincia da Computao" 
                                className="h-12 mr-3"
                                width="48" 
                                height="48"
                            />
                            <div className="border-l-2 border-gray-300 pl-3" aria-labelledby="site-title site-subtitle">
                                <h1 id="site-title" className="text-xl font-bold text-maroon-700">Cincia da Computao</h1>
                                <p id="site-subtitle" className="text-sm text-gray-600">Campus I - Campina Grande</p>
                            </div>
                        </div>
                        
                        {/* Desktop Menu */}
                        <nav className="hidden md:flex space-x-6" aria-label="Menu de navegao principal">
                            <Link to={getPath("/")} className="text-maroon-700 font-medium hover:text-maroon-900 transition" aria-current={window.location.pathname === getPath("/") ? "page" : undefined}>Incio</Link>
                            <Link to={getPath("/#sobre")} className="text-gray-700 font-medium hover:text-maroon-700 transition">Sobre</Link>
                            <Link to={getPath("/noticias")} className="text-gray-700 font-medium hover:text-maroon-700 transition" aria-current={window.location.pathname === getPath("/noticias") ? "page" : undefined}>Notcias</Link>
                            <Link to={getPath("/eventos")} className="text-gray-700 font-medium hover:text-maroon-700 transition" aria-current={window.location.pathname === getPath("/eventos") ? "page" : undefined}>Eventos</Link>
                            <Link to={getPath("/docentes")} className="text-gray-700 font-medium hover:text-maroon-700 transition" aria-current={window.location.pathname === getPath("/docentes") ? "page" : undefined}>Docentes</Link>
                            <Link to={getPath("/projetos")} className="text-gray-700 font-medium hover:text-maroon-700 transition" aria-current={window.location.pathname === getPath("/projetos") ? "page" : undefined}>Projetos</Link>
                            <Link to={getPath("/#contato")} className="text-gray-700 font-medium hover:text-maroon-700 transition">Contato</Link>
                            <Link to={getPath("/area-aluno")} className="bg-maroon-700 text-gray-700 px-4 py-2 rounded-md hover:bg-maroon-800 transition" aria-current={window.location.pathname === getPath("/area-aluno") ? "page" : undefined}>rea do Aluno</Link>
                        </nav>
                        
                        {/* Mobile Menu Button - MELHORADO */}
                        <button 
                            ref={menuButtonRef}
                            className="md:hidden flex items-center px-3 py-2 border rounded text-gray-700 border-gray-400 hover:text-maroon-700 hover:border-maroon-700 transition"
                            onClick={toggleMenu}
                            aria-expanded={isMobileMenuOpen}
                            aria-controls="mobile-menu"
                            aria-label="Menu de navegao"
                            aria-haspopup="true"
                            id="mobile-menu-button"
                        >
                            <span className="sr-only">{isMobileMenuOpen ? 'Fechar menu' : 'Abrir menu'}</span>
                            <span className="mr-2 text-sm font-medium">Menu</span>
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-hidden="true">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d={isMobileMenuOpen ? "M6 18L18 6M6 6l12 12" : "M4 6h16M4 12h16M4 18h16"} />
                            </svg>
                        </button>
                    </div>
                    
                    {/* Mobile Menu - MELHORADO */}
                    <div 
                        id="mobile-menu" 
                        ref={mobileMenuRef}
                        className={`md:hidden py-4 border-t border-gray-200 ${isMobileMenuOpen ? 'block' : 'hidden'}`}
                        role="navigation" 
                        aria-label="Menu de navegao mobile"
                        aria-labelledby="mobile-menu-button"
                    >
                        <div className="flex flex-col space-y-3">
                            <Link to={getPath("/")} className="text-maroon-700 font-medium hover:text-maroon-900 transition p-2 rounded hover:bg-gray-100" aria-current={window.location.pathname === getPath("/") ? "page" : undefined}>Incio</Link>
                            <Link to={getPath("/#sobre")} className="text-gray-700 font-medium hover:text-maroon-700 transition p-2 rounded hover:bg-gray-100">Sobre</Link>
                            <Link to={getPath("/noticias")} className="text-gray-700 font-medium hover:text-maroon-700 transition p-2 rounded hover:bg-gray-100" aria-current={window.location.pathname === getPath("/noticias") ? "page" : undefined}>Notcias</Link>
                            <Link to={getPath("/eventos")} className="text-gray-700 font-medium hover:text-maroon-700 transition p-2 rounded hover:bg-gray-100" aria-current={window.location.pathname === getPath("/eventos") ? "page" : undefined}>Eventos</Link>
                            <Link to={getPath("/docentes")} className="text-gray-700 font-medium hover:text-maroon-700 transition p-2 rounded hover:bg-gray-100" aria-current={window.location.pathname === getPath("/docentes") ? "page" : undefined}>Docentes</Link>
                            <Link to={getPath("/projetos")} className="text-gray-700 font-medium hover:text-maroon-700 transition p-2 rounded hover:bg-gray-100" aria-current={window.location.pathname === getPath("/projetos") ? "page" : undefined}>Projetos</Link>
                            <Link to={getPath("/#contato")} className="text-gray-700 font-medium hover:text-maroon-700 transition p-2 rounded hover:bg-gray-100">Contato</Link>
                            <Link to={getPath("/area-aluno")} className="bg-gray-700 text-white px-4 py-2 rounded-md hover:bg-gray-800 transition text-center" aria-current={window.location.pathname === getPath("/area-aluno") ? "page" : undefined}>rea do Aluno</Link>
                        </div>
                    </div>
                </div>
            </header>
        </div>
    );
}