import React from 'react';
import { Header } from '../header';
import { Footer } from '../footer';
// import calendarioImg from './calendario.png'; // Importe a imagem
import requerimentoPdf from './requerimento.pdf';

export function Formulario() {
  // Detecta se estamos em produo (GitHub Pages)
  const isProduction = process.env.NODE_ENV === 'production';
  // Base path para GitHub Pages
  const pdfPath = isProduction ? '/computacao-site'+requerimentoPdf.slice(1) : requerimentoPdf;
  const PDFViewer = () => {
    return (
      <div className="my-8">
        <iframe
          src={pdfPath}
          width="100%"
          height="900px"
          title="Requerimento"
        />
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gray-50 text-gray-800 font-sans">
      <main className="container mx-auto px-4 py-12">
        <h1 className="text-4xl font-bold text-center text-maroon-800 mb-10">
          Formulrios e Requerimento
        </h1>
          <h2>
            Formulrio de requerimento geral - Deve ser preenchido, assinado e enviado para o e-mail da coordenao para qualquer solicitao geral.
          </h2>
        <PDFViewer />
      </main>
    </div>
  );
}