        // --- TPICOS ESPECIAIS (ELETIVAS) --- // O PDF lista vrias eletivas, mas o plano de integralizao mostra apenas 3 genricas. // Aqui esto as ementas para todas as eletivas listadas na seo 14. EMENTAS. // Elas no tm semestre fixo, o aluno escolhe quando cursar. { codigo: "CPT01106", nome: "TPICOS ESPECIAIS EM BANCO DE DADOS", semestre: null, // Semestre no fixo, eletiva cargaHoraria: "60h", categoria: "eletiva", prerequisitos: [], ementa: "A ser definida por professores na poca de sua oferta, em funo da demanda e focando trabalhos de concluso de curso.", referencias: { basica: ["A ser definida por professores na poca de sua oferta."], complementar: [] } }, { codigo: "CPT01113", nome: "TPICOS ESPECIAIS EM COMPUTAO", semestre: null, cargaHoraria: "60h", categoria: "eletiva", prerequisitos: [], ementa: "A ser definida por professores na poca de sua oferta, em funo da demanda e focando trabalhos de concluso de curso.", referencias: { basica: ["A ser definida por professores na poca de sua oferta."], complementar: [] } },
